const BOT_TOKEN = "7895164901:AAGXXJa5CxwySIDDIEmCpPCcijdBqKI6ttQ";
const CHAT_ID = "945872364";

export const sendToTelegram = (
  name: string,
  testName: string,
  score: number,
  grade: number
) => {
  const message = `
 * *  
 : *${name}*  
 : *${testName}*  
 : *${score}%*  
 : *${grade}*
`;

  fetch(`https://api.telegram.org/bot${BOT_TOKEN}/sendMessage`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      chat_id: CHAT_ID,
      text: message,
      parse_mode: "Markdown",
    }),
  })
    .then((res) => res.json())
    .then((data) => {
      if (data.ok) {
        alert(" !");
      } else {
        alert("  .");
      }
    })
    .catch((error) => {
      alert(" .");
      console.error(error);
    });
};
