import React from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Typography,
  Button,
} from "@mui/material";
import { testData } from "../data/testData";

interface ResultDialogProps {
  open: boolean;
  onClose: () => void;
  onSend: () => void;
  name: string;
  testKey: keyof typeof testData;
  score: number;
  grade: number;
}

const ResultDialog: React.FC<ResultDialogProps> = ({
  open,
  onClose,
  onSend,
  name,
  testKey,
  score,
  grade,
}) => {
  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle> </DialogTitle>
      <DialogContent>
        <Typography>
          {name},     <b>{testData[testKey].name}</b>.
        </Typography>
        <Typography>
           : <b>{score}%</b>
        </Typography>
        <Typography>
          : <b>{grade}</b>
        </Typography>
        {grade === 2 ? (
          <Typography color="error">   !</Typography>
        ) : (
          <Typography color="success">   !</Typography>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="secondary">
          
        </Button>
        <Button onClick={onSend} color="primary">
           
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ResultDialog;
